import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { AuthService } from '../../Services/auth.service';
import { MdbTableDirective } from 'angular-bootstrap-md';
import { Router } from "@angular/router";

@Component({
  selector: 'app-management',
  templateUrl: './management.component.html',
  styleUrls: ['./management.component.css']
})
export class ManagementComponent implements OnInit {

  @ViewChild(MdbTableDirective, { static: true }) mdbTable: MdbTableDirective;
  userInfo;
  allUsers;
  subscriber;
  allCompanies;
  editField: string;
  numberOfUsers: number;
  headElementsUsers = [
    'ID',
    'Nombres Completos',
    'Mayor de Edad',
    'Nombre Artstico',
    'Gnero',
    'Alias',
    'Fecha de Nacimiento',
    'Pas',
    'Talla de Pantaln',
    'Talla de Camisa',
    'Talla de Chaqueta',
    'Pie',
    'Altura',
    'Color de Piel',
    'Color de Pelo',
    'Color de Ojos',
    'Modelo de Coche',
    'Modelo de Moto',
    'Raza de Mascota',
    'ltimos Trabajos',
    'Nmero DNI',
    'Nmero de Seguridad Social',
    'Correo de Contacto',
    'Contrasea',
    'Telfono de Contacto',
  ];
  headElementsCompanies = [
    'ID',
    'Nombre de Usuario',
    'Correo',
    'Telfono de Contacto',
    'Contrasea',
    'Remove'
  ];

  searchText = '';
  previous: string;

  constructor(
    public authService: AuthService,
    private router: Router,
  ) {
  }

  @HostListener('input') oninput() {
    this.searchItems();
  }

  ngOnInit() {
    this.authService.getAllCompanies()
      .subscribe(resp => {
        this.allCompanies = resp;
      });
    this.authService.findByToken()
      .subscribe(res => this.userInfo = res);

    /* this.subscriber = this.authService.findByToken().subscribe(
       res => {
         this.userInfo = res;
       });*/
    this.authService.getAllUsers()
      .subscribe(res => {
        this.allUsers = res;
      });
    for (const user of this.allUsers) {
      if (user.mayorEdad) {
        user.mayorEdad = 'SI';
      } else {
        user.mayorEdad = 'NO';
      }
    }
    this.mdbTable.setDataSource(this.allUsers);
    this.previous = this.mdbTable.getDataSource();
  }


  searchItems() {
    const prev = this.mdbTable.getDataSource();

    if (!this.searchText) {
      this.mdbTable.setDataSource(this.previous);
      this.allUsers = this.mdbTable.getDataSource();
    }

    if (this.searchText) {
      this.allUsers = this.mdbTable.searchLocalDataBy(this.searchText);
      this.mdbTable.setDataSource(prev);
    }
  }
  updateList(id: number, property: string, event: any) {
    const editField = event.target.textContent;
    this.allCompanies[id][property] = editField;
  }

  removeCompany(userId) {
    this.allUsers.splice(userId, 1);
    this.authService.deleteUser(userId).subscribe(res => console.log(res));
  }

  changeValue(id: number, property: string, event: any) {
    this.editField = event.target.textContent;
  }


}
